import React from "react";
import { motion } from "framer-motion";

export function PageHeader() {
  return (
    <>
      <motion.header
        className="page-header"
        initial={{ y: -50, opacity: 0 }}
        animate={{ y: 0, opacity: 1 }}
        transition={{ duration: 1 }}
      >
        <h1>Semester 5</h1>
        <h3>Session Link</h3>
      </motion.header>
    </>
  );
}
