export const HandleCopy = (copyContent) => {
  let textField = document.createElement("textarea");
  textField.innerText = copyContent;
  document.body.appendChild(textField);
  textField.select();
  document.execCommand("copy");
  textField.remove();

  document.getElementById(copyContent).classList.add("animate-cp-logo");
  setTimeout(() => {
    document.getElementById(copyContent).classList.remove("animate-cp-logo");
  }, 500);
};
