import { WeekDay } from "./WeekDay";

export const LectureTime = (
  hour,
  startHour,
  endHour,
  minute,
  startMinute,
  endMinute,
  subjectName,
  day
) => {
  //console.log(hour, minute);
  // hour = 15;
  // minute = 1;
  if (
    WeekDay[new Date().getDay()] === day &&
    hour >= startHour &&
    hour <= endHour
  ) {
    if (hour === startHour && minute >= startMinute) {
      console.log(hour, minute);
      return true;
    } else if (hour === endHour && minute <= endMinute) {
      console.log(hour, minute, endMinute);
      return true;
    } else if (
      endMinute === 0 &&
      hour === startHour + 1 &&
      hour === endHour - 1
    ) {
      console.log(hour, minute);
      return true;
    } else {
      console.log("no if");
      return false;
    }
  }
  return false;
};
