import React, { useEffect, useState } from "react";
import { SubjectData } from "./SubjectData";
import { HandleCopy } from "./HandleCopy";
import { LectureTime } from "./LectureTime";
import { WeekDay } from "./WeekDay";
import uuid from "react-uuid";
import { motion } from "framer-motion";

export function SubjectCard() {
  const [hour, setHour] = useState(-1);
  const [minute, setMinute] = useState(-1);

  useEffect(() => {
    const intervalID = setInterval(() => {
      tick();
    }, 1000);
    return () => {
      clearInterval(intervalID);
    };
  }, []);
  function tick() {
    let newDate = new Date();
    setMinute(newDate.getMinutes());
    setHour(newDate.getHours());
    //console.log(hour, minute, newDate.getSeconds());
  }
  return (
    <motion.div
      className="container"
      initial={{ opacity: 0, y: 150 }}
      animate={{ opacity: 1, y: 0 }}
      transition={{ duration: 1.5 }}
    >
      <div className="row flex-container">
        {hour > -1 &&
          minute > -1 &&
          SubjectData.map((item) => {
            return (
              <div
                className="col-xxl-3 col-xl-4 col-lg-6 col-md-6 col-sm-12 col-xs-12"
                key={uuid()}
              >
                <div className="card">
                  <div className="card-body">
                    <hr />
                    <h4 className="text-center">{item.subjectName}</h4>
                    <p className="text-center">{item.l_t}</p>
                    {item.electiveSubject && (
                      <p className="text-muted text-center">Elective</p>
                    )}
                    <a
                      href={item.zoomLink}
                      className="btn btn-primary"
                      target="_blank"
                      rel="noreferrer"
                    >
                      Zoom Link
                    </a>
                    <table className="table data-table">
                      <tbody>
                        <tr>
                          <td>Meeting ID</td>
                          <th>{item.meetingID}</th>
                          <td>
                            <i
                              className="far fa-copy"
                              id={item.meetingID}
                              // onClick={() => {
                              //   navigator.clipboard.writeText(item.meetingID);
                              // }}
                              onClick={() => HandleCopy(item.meetingID)}
                            ></i>
                          </td>
                        </tr>
                        <tr>
                          <td>Passcode</td>
                          <th>{item.passcode}</th>
                          <td>
                            <i
                              className="far fa-copy"
                              id={item.passcode}
                              onClick={() => HandleCopy(item.passcode)}
                            ></i>
                          </td>
                        </tr>
                      </tbody>
                    </table>
                    <table className="table table-bordered text-center">
                      <tbody>
                        {item.dayTiming.map((subItem, index, values) => {
                          let now = new Date();
                          // console.log(
                          //   now.getHours().toFixed() +
                          //     " " +
                          //     now.getMinutes().toLocaleString()
                          // );
                          //console.log(weekDay[now.getDay()]);
                          if (
                            index > 0 &&
                            subItem.day === values[index - 1].day
                          ) {
                            return null;
                          }
                          if (
                            index < values.length - 1 &&
                            subItem.day === values[index + 1].day
                          ) {
                            return (
                              <>
                                <tr
                                  key={uuid()}
                                  className={
                                    WeekDay[now.getDay()] === subItem.day
                                      ? "currentDay"
                                      : ""
                                  }
                                >
                                  <td rowSpan="2">{subItem.day}</td>
                                  <td
                                    className={
                                      LectureTime(
                                        hour,
                                        subItem.startHour,
                                        subItem.endHour,
                                        minute,
                                        subItem.startMinute,
                                        subItem.endMinute,
                                        item.subjectName,
                                        subItem.day
                                      )
                                        ? "currentLecture"
                                        : ""
                                    }
                                  >
                                    {subItem.time}
                                  </td>
                                </tr>
                                <tr
                                  key={uuid()}
                                  className={
                                    WeekDay[now.getDay()] === subItem.day
                                      ? "currentDay"
                                      : ""
                                  }
                                >
                                  <td
                                    className={
                                      LectureTime(
                                        hour,
                                        values[index + 1].startHour,
                                        values[index + 1].endHour,
                                        minute,
                                        values[index + 1].startMinute,
                                        values[index + 1].endMinute,
                                        item.subjectName,
                                        values[index + 1].day
                                      )
                                        ? "currentLecture"
                                        : ""
                                    }
                                  >
                                    {values[index + 1].time}
                                  </td>
                                </tr>
                              </>
                            );
                          } else {
                            return (
                              <tr
                                key={uuid()}
                                className={
                                  WeekDay[now.getDay()] === subItem.day
                                    ? "currentDay"
                                    : ""
                                }
                              >
                                <td>{subItem.day}</td>
                                <td
                                  className={
                                    LectureTime(
                                      hour,
                                      subItem.startHour,
                                      subItem.endHour,
                                      minute,
                                      subItem.startMinute,
                                      subItem.endMinute,
                                      item.subjectName,
                                      subItem.day
                                    )
                                      ? "currentLecture"
                                      : ""
                                  }
                                >
                                  {subItem.time}
                                </td>
                              </tr>
                            );
                          }
                        })}
                      </tbody>
                    </table>
                    <hr />
                  </div>
                </div>
              </div>
            );
          })}
      </div>
    </motion.div>
  );
}
