import React, { useEffect, useState } from "react";
import { motion } from "framer-motion";

export function Footer() {
  const [darkMode, setDarkMode] = useState(false);
  function toggleDarkMode() {
    setDarkMode((darkMode) => !darkMode);
  }
  useEffect(() => {
    if (darkMode) {
      document.getElementById("rootHTML").classList.add("darkMode");
    } else {
      document.getElementById("rootHTML").classList.remove("darkMode");
    }
  }, [darkMode]);
  return (
    <div className="footerDiv">
      <motion.button
        initial={{ y: "-100vh", opacity: 0 }}
        animate={{ y: 0, opacity: 1 }}
        transition={{ delay: 1, duration: 1.5, type: "spring", stiffness: 50 }}
        type="button"
        className="btn btn-darkMode"
        onClick={toggleDarkMode}
      >
        <i className={darkMode ? "far fa-sun" : "fas fa-moon"}></i>
      </motion.button>
      <motion.a
        initial={{ y: "-100vh", opacity: 0 }}
        animate={{ y: 0, opacity: 1 }}
        transition={{ delay: 1, duration: 1.5, type: "spring" }}
        href="https://vickygupta16.github.io/#/"
        target="_blank"
        rel="noreferrer"
        className="btn btnPF"
        type="button"
        data-tooltip-content="Contact Developer"
      >
        <i className="fas fa-laptop-code"></i>
      </motion.a>
    </div>
  );
}
